import { Checkbox, FormControlLabel, Grid } from "@mui/material"; import { useForm } from "react-hook-form";
import { IDivorceData } from "../models/divorcedata";
import { XTextField as TextField } from "../components/TextField";
import { DiverceMethodOptions, DivorceMethod, DivorcePaperType, DivorcePaperTypes, MarriageType, MarriageTypeOptions, SocialStatusCodes, Towns, shouldPreviousFianceesExist } from "../constants";
import { IStepRef } from "../utils";
import React, { useImperativeHandle } from "react";
import { popupActions } from "../store/popupReducer";

interface IDivorceDataProps {
    defaultValues?: IDivorceData;
}

export const DivorceData = React.forwardRef<IStepRef, IDivorceDataProps>((props, ref) => {

    const form = useForm<IDivorceData>({ defaultValues: props.defaultValues });
    const marriageType = form.watch("marriageType");
    const divorcePaperType = form.watch("divorcePaperType");
    useImperativeHandle(ref, () => ({
        onNext: async (func) => {
            func(form.getValues());
            return true;
        }
    }))
    return (
        <Grid container rowSpacing={3} columnSpacing={3}>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="marriageType" type="select" options={MarriageTypeOptions} />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="docOffice" type="select" options={Towns} />
            </Grid>
            <Grid item xs={4}>
                {marriageType === MarriageType.inCountry && (
                    <TextField control={form.control} label="" name="divorcePaperType" type="select" options={DivorcePaperTypes} />
                )}
            </Grid>
            {divorcePaperType === DivorcePaperType.Machined && (
                <>
                    <Grid item xs={4}>
                        <TextField control={form.control} label="" name="year" type="number" />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField control={form.control} label="" name="letter" />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField control={form.control} label="" name="serial" />
                    </Grid>
                </>
            )}
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="marriageDate" type="date" />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="mosadqaLate" />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="earlyMosadqa" />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="divorceType" type="select" options={[{ label: " ", value: "1" }]} />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="divorceMethod" type="select" options={DiverceMethodOptions} />
            </Grid>
            <Grid item xs={4}>
                <TextField control={form.control} label=" " name="hadSex" type="select" options={[{ label: " ", value: String(true) }, { label: "  ", value: String(false) }]} />
            </Grid>
            <Grid item xs={6}>
                <TextField control={form.control} label=" " name="divorceDate" type="date" />
            </Grid>
            <Grid item xs={6}>
                <TextField control={form.control} label="  " name="previousDivorcesNo" type="number" />
            </Grid>
            <Grid item xs={6}>
                <TextField control={form.control} label="" name="notes" type="string" multiline rows={4} />
            </Grid>
        </Grid>
    );
});